<template>
  <div class="user-box">
    <HeadTop title="我的资产" :isShowTop="true" :isSubHeader="false"></HeadTop>
    <div class="top-banner">
       <div class="title">人人都可以做主播</div>
       <div class="desc">语音直播工作室</div>
       <div class="span-btn">
          火热进行中
       </div>
    </div>
    <div class="user-item">
      <div class="title">我的代币资产</div>
      <div class="token-box flex-between">
        <div class="token-item">
        <div class="name">USDT</div>
        <div class="num">{{usdtBalance}}</div>
      </div>
      <div class="token-item">
        <div class="name">KTT</div>
        <div class="num">{{kttBalance}}</div>
      </div>
      <div class="token-item">
        <div class="name">KTM</div>
        <div class="num">{{ktmBalance}}</div>
      </div>
      </div>
    </div>
    <div class="user-item">
      <div class="title">我的业绩</div>
      <div class="nft-box flex-between">
        <!-- <div class="nft-item">
        <div class="name">全网业绩</div>
        <div class="num">{{totalPerformance}}</div>
      </div> -->
      <div class="nft-item">
        <div class="name">我的业绩</div>
        <div class="num">{{investAmount}}</div>
      </div>
      <div class="nft-item">
        <div class="name">小区业绩</div>
        <div class="num">{{teamAmount}}</div>
      </div>
      </div>
    </div>
     <div class="user-item">
      <div class="title">我的NFT</div>
      <div class="mynft-box">
        <div class="mynft-item flex-middle" v-for="item in nftArr" :key="item.id">
          <img class="nft-logo" src="@/assets/images/item_1.png" alt="">
          <div class="desc-box flex-item">
            <div class="name">ID：{{item.id}}</div>
            <div class="num">收益: {{item.reward}}KTT</div>
          </div>
          <div class="get-btn" @click="getNftReward(item.id)">领取</div>
      </div>
      </div>
    </div>
  </div>
</template>
<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch } from "vue";
import { useStore } from "vuex";
import config from "@/utils/config";
import swapAbi from "@/utils/swapAbi";
import tokenAbi from "@/utils/abi";
import nftAbi from "@/utils/nftAbi.json";
import BigNumber from 'bignumber.js';
import { Toast } from 'vant';
import { Multicall } from 'ethereum-multicall';
export default {
  components: {
    HeadTop
  },
  setup () {
    const store = useStore()
    const data = reactive({
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      swapContract: null,
      usdtContract: null,
      kttContract: null,
      ktmContract: null,
      nftContract: null,
      usdtBalance: '0.00',
      kttBalance: '0.00',
      ktmBalance: '0.00',
      teamAmount: '0.00',
      investAmount: '0.00',
      totalPerformance: '0.00',
      nftArr: [],
      initContract: () => {
        data.swapContract = new data.web3.eth.Contract(swapAbi, config.swapAddress)
        data.usdtContract = new data.web3.eth.Contract(tokenAbi, config.usdtAddress)
        data.kttContract = new data.web3.eth.Contract(tokenAbi, config.kttAddress)
        data.ktmContract = new data.web3.eth.Contract(tokenAbi, config.ktmAddress)
        data.nftContract = new data.web3.eth.Contract(nftAbi, config.nftAddress)
        data.initData()
      },
      initData: () => {
        data.getBalance()
        data.getNftData()
        data.getPerformance()
      },
       getBalance: async () => {
        let kttDecimals = await data.kttContract.methods.decimals().call()
        let usdtDecimals = await data.usdtContract.methods.decimals().call()
        let ktmDecimals = await data.ktmContract.methods.decimals().call()
        let kttBalance = await data.kttContract.methods.balanceOf(data.address).call()
        let usdtBalance = await data.usdtContract.methods.balanceOf(data.address).call()
        let ktmBalance = await data.ktmContract.methods.balanceOf(data.address).call()
        data.kttBalance =  new BigNumber(kttBalance).dividedBy(10 ** kttDecimals).toFixed(2,1)
        data.usdtBalance =  new BigNumber(usdtBalance).dividedBy(10 ** usdtDecimals).toFixed(2,1)
        data.ktmBalance =  new BigNumber(ktmBalance).dividedBy(10 ** ktmDecimals).toFixed(2,1)
      },
      getPerformance: async () => {
        let totalPerformance = await data.swapContract.methods.totalPerformance().call()
        let userInfo = await data.swapContract.methods.getUserInfo(data.address).call()
        data.totalPerformance = new BigNumber(totalPerformance).dividedBy(10 ** 18).toFixed(2)
        data.teamAmount = new BigNumber(userInfo.teamAmount).dividedBy(10 ** 18).toFixed(2)
        data.investAmount = new BigNumber(userInfo.investAmount).dividedBy(10 ** 18).toFixed(2)
      },
      getNftData: async () => {
        let ids = await data.nftContract.methods.getUserAllTokens(data.address).call()
        let kttDecimals = await data.kttContract.methods.decimals().call()
        const multicall = new Multicall({ multicallCustomContractAddress: config.multicallAddress,web3Instance: data.web3, tryAggregate: false });
        let callArr = []
         for (let index = 0; index < ids.length; index++) {
          const id = ids[index];
          callArr.push(
            { reference: 'reward', methodName: 'getCanReward', methodParameters: [id]}
          )
        }
        let contractCallContext = [
          {
              reference: 'swapContract',
              contractAddress: config.swapAddress,
              abi: swapAbi,
              calls: callArr
          }
        ]
         const list = await multicall.call(contractCallContext)
         let result = list.results.swapContract.callsReturnContext
         console.log(result)
         let arr = []
         for (let i = 0; i < result.length; i++) {
           let item = result[i];
           arr.push({
             id: item.methodParameters[0],
             reward: new BigNumber(data.web3.utils.hexToNumberString(item.returnValues[0].hex)).dividedBy(10** kttDecimals).toFixed(3)
           })
         }
         data.nftArr = arr
      },
      getNftReward: async (id) => {
         try {
          await data.swapContract.methods.getNftReward(id).send({from: data.address})
          Toast('领取成功')
           data.initData()
        } catch (error) {
          Toast('领取失败')
        }
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.user-box{
  padding: 15px 16px;
  .top-banner{
    padding: 16px;
    background: url('~@/assets/images/item_bg.png') no-repeat;
    background-size: cover;
    border-radius: 4px;
    margin-bottom: 30px;
    .title{
      font-size: 23px;
      color: #fff;
      font-weight: 600;
      margin-bottom: 5px;
    }
    .desc{
      font-size: 14px;
      color: rgba(255, 255, 255, 0.55);
      margin-bottom: 15px;
    }
    .span-btn{
      height: 22px;
      line-height: 22px;
      font-size: 10px;
      background: linear-gradient(0deg, rgba(255, 255, 255, 0.8) 0%, #FFFFFF 100%);
      color: #F03C85;
      padding: 0 10px;
      border-radius: 12px;
      display: inline-block;
    }
  }
  .user-item{
    margin-bottom: 24px;
    .title{
      font-size: 16px;
      color: #fff;
      font-weight: 600;
      margin-bottom: 14px;
      position: relative;
      padding-left: 10px;
      &::after{
        content: '';
        width: 3px;
        position: absolute;
        height: 100%;
        left: 0;
        background: #723EE9;
      }
    }
    .token-box{
      flex-wrap: wrap;
    }
    .token-item{
      padding: 20px;
      color: #fff;
      margin: 5px 0;
      border-radius: 6px;
      width: 49%;
      .name{
        font-size: 16px;
        font-weight: 600;
      }
      .num{
        font-weight: 600;
        font-size: 22px;
      }
      &:nth-child(1) {
        background: url('~@/assets/images/item_1.png') no-repeat;
        background-size: cover;
        .name{
          color: #FFE3C8;
        }
      }
      &:nth-child(2) {
        background: url('~@/assets/images/item_2.png') no-repeat;
        background-size: cover;
        .name{
          color: #C3E3FE;
        }
      }
      &:nth-child(3) {
        background: url('~@/assets/images/item_2.png') no-repeat;
        background-size: cover;
        .name{
          color: #C3E3FE;
        }
      }
    }
    .nft-box{
      background: linear-gradient(-90deg, #3C3863 0%, #4A3B76 100%);
      border-radius: 4px;
      padding: 0 20px;
      flex-wrap: wrap;
      .nft-item{
        width: 50%;
         padding:20px 15px;
        .name{
          color: #9D8BD0;
          font-size: 14px;
          margin-bottom: 10px;
        }
        .num{
           color: #fff;
          font-size: 18px;
          font-weight: 600;
        }
      }
    }
    .mynft-box{
      .mynft-item{
        background: linear-gradient(-90deg, #3C3863 0%, #4A3B76 100%);
        padding: 10px;
        border-radius: 4px;
        margin-bottom: 10px;
        img{
          width: 60px;
          height: 60px;
          margin-right: 12px;
          display: block;
        }
        .desc-box{
          .name{
            font-size: 18px;
            color: #fff;
            font-weight: 600;
            margin-bottom: 5px;
          }
          .num{
            font-size: 14px;
            color: #9D8BD0;
          }
        }
        .get-btn{
          background: linear-gradient(270deg, #00DFB2 0%, #00C4DE 100%);
          height: 30px;
          border-radius: 15px;
          padding: 0 15px;
          color: #195A61;
          line-height: 30px;
          font-size: 13px;
        }
      }
    }
  }
}
</style>
